ed to maintain accurate, up-to-date, and complete details in their account. Failure to keep accurate, up-to-date, and complete account details may result in the Users&#39; inability to access and use Services or Vehitra World&#39;s termination of this Agreement with the User. </p><p class=\"text-theme-gray-4 font-light text-size-6\"> 4. Communication via email. Legally binding statements can be delivered to the User using the e-mail address stored in the account. </p><p class=\"text-theme-gray-4 font-light text-size-6\"> 5. Passwords. The User is required to keep his password secret. S/He is not entitled to allow third parties to use his or her user account. As soon as the User is aware or has reason to believe that a third party is accessing his/her login details or otherwise gained or may gain access to his/her user account, s/he is obliged to notify Vehitra World about this fact immediately. Vehitra World is then entitled to block the User account until clarification of the issue. </p><p class=\"text-theme-gray-4 font-light text-size-6\"> 6. Liability for misuse of the user account. The User is liable to Vehitra World for all actions, which are carried out using Users&#39; account unless the User is not responsible for such misuse. </p><p class=\"text-theme-gray-4 font-light text-size-6\"> 7. Termination. The Platform User Agreement is entered into for an indefinite duration. The User can terminate this agreement at any time without prior notice by declaring in written form by mail or hand delivery to Vehitra World Office 312, Building 60, Road 1701, Block 317 Diplomatic Area, Manama Bahrain, or by email from the email address associated with the User account to: <a class=\"hover:underline text-theme-active\" href=\"mailto:bookings@vehitraworld.com\">bookings@vehitraworld.com</a> . Vehitra World can also terminate this agreement with a User at any time without prior notice by communicating by email to the email address associated with the User account. </p></section><section class=\"flex flex-col gap-6 md:ml-6\"><h4 class=\"text-theme-gray-4 text-size-4 md:text-size-6 font-bold\">Service Agreements - Contractual Relationship &amp; Conclusion</h4><p class=\"text-theme-gray-4 font-light text-size-6\"> 1. Contractual Relationship. Vehitra World enables Users to arrange and schedule Services with Service Providers (i.e. licensed chauffeur/limousine companies).Vehitra World arranges agreements on Services (Service Agreement) which are concluded between the User and the Service Provider directly. Thus, Users are entitled to file claims against Service Providers directly. The User acknowledges that Vehitra World serves strictly as an intermediate or broker and not as a transportation service provider. The User shall have no claim to the formation or arrangement of Service Agreements. </p><p class=\"text-theme-gray-4 font-light text-size-6\"> 2. Conclusion of Service Agreements. Service Agreements between the User and Service Providers are concluded through the Platform as follows: First, the User indicates the desired service (e.g., one-way or hourly booking) and fills in the booking screen of the Platform. The fee for the Services will then be displayed for each booking class. The User then chooses a booking class and confirms his/her payment method, the associated billing details, and contact information supporting the provision of Services. The User can review and correct the details of his/her Service request (Booking Request) before submitting the Booking Request by clicking the button BOOK NOW &amp; PAY (or a button with a similar indication). When a User submits a Booking Request whether via the Platform or by phone or by email to Vehitra World, the User makes an offer for the conclusion of a Service Agreement with a Service Provider. Upon receiving a Booking Request, Vehitra World sends to the User an email to confirm the receipt of the Booking Request, including the details that were received. The Service Agreement between the User and the Service Provider only comes into effect upon Vehitra World&#39;s submission of a separate statement (Ride Details) by email shortly before the ride, stating the name and contact details of the selected Service Provider. If Vehitra World cannot find a Service Provider to fulfill the Booking Request, Vehitra World will inform the User by email that the Booking Request cannot be fulfilled. In addition to these T&amp;Cs, the Booking Conditions applicable at the time of booking shall apply. </p></section><section class=\"flex flex-col gap-6 md:ml-6\"><h4 class=\"text-theme-gray-4 text-size-4 md:text-size-6 font-bold\">The Cancellation of Service Agreements</h4><p class=\"text-theme-gray-4 font-light text-size-6\"> 1. Cancellation conditions. The User may cancel Service Agreements free of charge or with costs according to the conditions which apply to the respective booking class agreed upon in the respective Service Agreement. </p><p class=\"text-theme-gray-4 font-light text-size-6\"> 2. Cancellation only via Platform. To warrant a smooth and safe transaction, all cancellation requests must be made through the website <a class=\"hover:underline text-theme-active\" href=\"https://www.vehitraworld.com\">www.vehitraworld.com</a>. Cancellations are not possible by email. Vehitra World will confirm valid cancellations to the User by email. </p></section><section class=\"flex flex-col gap-6 md:ml-6\"><h4 class=\"text-theme-gray-4 text-size-4 md:text-size-6 font-bold\">The payment of Fees due under the Service Agreements</h4><p class=\"text-theme-gray-4 font-light text-size-6\"> 1. Fees are due. All fees agreed upon in Service Agreements include possible charges and the statutory tax, if applicable. Unless agreed otherwise, the fee for bookings by route applies to the agreed journey without stopovers and without detours. In case of bookings by time or by route, the User must still pay the agreed amount of the fee if the passenger ends the journey early or shortens it. </p><p class=\"text-theme-gray-4 font-light text-size-6\"> 2. Invoices. Vehitra World will send an invoice of the fees to the User (exclusively) by email on behalf of the Service Provider. </p><p class=\"text-theme-gray-4 font-light text-size-6\"> 3. Payment method. The fee shall be paid using the payment method chosen by the User during the booking process. For payments upon invoice, the fee is payable within 10 days after notification of the invoice. </p></section><section class=\"flex flex-col gap-6 md:ml-6\"><h4 class=\"text-theme-gray-4 text-size-4 md:text-size-6 font-bold\"> Liability of Vehitra World under the Platform User Agreement and of Service Providers under Service Agreements </h4><p class=\"text-theme-gray-4 font-light text-size-6\"> 1. Vehitra World shall only be liable towards the User, in particular in regards to its contractual obligations under the Platform User Agreement and any obligations in regards to the arrangement of Service Agreements, according to this Section VI. In no event shall Vehitra World be liable for any actions or omissions by Service Providers, in particular under any Service Agreement. Neither Service Providers nor drivers it employs or subcontracts are vicarious agents nor subcontractors of Vehitra World. </p><p class=\"text-theme-gray-4 font-light text-size-6\"> 2. The liability of Vehitra World towards the User, in particular according to the Platform User Agreement, and the liability of Service Providers towards the User, in particular, according to Service Agreements, are governed by the following rules: </p><p class=\"text-theme-gray-4 font-light text-size-6\"> 3. Vehitra World and the Service Provider respectively assume unlimited liability for damages caused willfully or as a result of gross negligence by them, by their employees, and by their vicarious agents, as well as in the event of fraudulent non-disclosure of defaults, explicit guarantees, and damages arising from personal injuries (life, body, and health). </p><p class=\"text-theme-gray-4 font-light text-size-6\"> 4. Vehitra World and the Service Provider respectively shall only be liable for other damages if they are in violation of an obligation, which must be observed to make the due performance of the agreement possible and on whose observation the other party to the agreement can usually rely. At the same time, the liability for damages is limited to those damages, which are deemed typical for this type of contract and foreseeable. Possible liabilities according to product liability law remain unaffected. </p><p class=\"text-theme-gray-4 font-light text-size-6\"> 5. Vehitra World does not warrant or assume liability for obligations according to arranged Service Agreements. In particular, Vehitra World is then not liable for non-compliance with regulations under public law by Service Providers. </p><p class=\"text-theme-gray-4 font-light text-size-6\"> 6. Furthermore, Vehitra World shall not be liable for the accuracy, reliability, and completeness of the contents and programs provided by Vehitra World on the Platform free of charge as well as damages, which may arise from them. </p></section><section class=\"flex flex-col gap-6 md:ml-6\"><h4 class=\"text-theme-gray-4 text-size-4 md:text-size-6 font-bold\"> Final Provisions for both the Platform User Agreement and the Service Agreements </h4><p class=\"text-theme-gray-4 font-light text-size-6\"> 1. Vehitra World is entitled to completely or partially assign all rights and obligations derived from the Platform User Agreement to a third party giving 4 weeks prior notice; in this case, the User may terminate the User Agreement for good cause. </p><p class=\"text-theme-gray-4 font-light text-size-6\"> 2. Force majeure. If events or circumstances outside Vehitra World&#39;s or a Service Providers&#39; sphere of influence (force majeure) make the performance of their respective contractual obligations impossible, Vehitra World or the Service Provider, whoever is 