 type of applications installed on a mobile device. Though none of the information could determine who you are, we still consider it personal information. Using social media would give us access to more of your personal information. </p><h3 class=\"text-theme-gray-4 text-size-4 md:text-size-6 font-bold\"> What will my information be used for? </h3><ul class=\"flex flex-col gap-3 mt-6 list-square ml-10\"><li class=\"text-theme-gray-4 font-light text-size-6\"> Complete your booking and inform the car rental provider of your details at the destination. </li><li class=\"text-theme-gray-4 font-light text-size-6\"> To allow our customer service team to assist you in case of any issues relative to your booking. </li><li class=\"text-theme-gray-4 font-light text-size-6\"> To send information regarding the occasional promotions that you may have chosen. </li><li class=\"text-theme-gray-4 font-light text-size-6\">To contact you for urgent matters such as incomplete bookings, additional information to complete your booking, or last-minute requests that need urgent attention. This might require us to contact you by email, phone, etc. </li></ul><h3 class=\"text-theme-gray-4 text-size-4 md:text-size-6 font-bold\"> When and with whom do we share your data? </h3><ul class=\"flex flex-col gap-3 mt-6 list-square ml-10\"><li class=\"text-theme-gray-4 font-light text-size-6\"> To complete your bookings, we would need to share your information with the car rental company at your destination. </li><li class=\"text-theme-gray-4 font-light text-size-6\"> Through other portals associated with us and using our engine to process bookings. </li><li class=\"text-theme-gray-4 font-light text-size-6\"> In case of legal issues, we might be required by any legal authorities to disclose your personal information. </li><li class=\"text-theme-gray-4 font-light text-size-6\"><p class=\"text-theme-gray-4 text-size-4 md:text-size-6 font-bold\">Google Analytics</p><div class=\"flex flex-col gap-6\"><p cla